import { PanelPlugin } from '@grafana/data';
import { ImageSaveOption } from './ImageSaveOption';
import { ImageSavePanel } from './ImageSavePanel';
import { ImageSaveOptionPanel } from './ImageSaveOptionPanel';
export const plugin = new PanelPlugin<ImageSaveOption>(ImageSavePanel).setPanelOptions((builder) => {
  builder
    .addCustomEditor({
      id: 'context',
      path: 'context',
      name: '',
      description: '',
      editor: ImageSaveOptionPanel,
      defaultValue: {
        image:
          'data:image/png;base64,',
        width: 400,
        height: 400,
      },
    })
    .addBooleanSwitch({
      path: 'hasLink',
      name: 'Has Link',
      defaultValue: false,
    })
    .addTextInput({
      path: 'hyperlink',
      name: 'Link',
      description: 'Link to open when clicked',
      defaultValue: '',
      showIf: (config) => config.hasLink,
    })
    .addTextInput({
      path: 'linkTitle',
      name: 'Link Title',
      description: 'Link title to show when hover',
      defaultValue: '',
      showIf: (config) => config.hasLink,
    })
    .addBooleanSwitch({
      path: 'hasBackground',
      name: 'Has Background',
      defaultValue: false,
    })
    .addColorPicker({
      path: 'backgroundColor',
      name: 'Background Color',
      defaultValue: 'rgba(100,100,100,1)',
      showIf: (config) => config.hasBackground,
    })
    .addBooleanSwitch({
      path: 'showIsResponsive',
      name: 'Is Responsive',
      description: 'Show responsive image for panel',
      defaultValue: true,
    })
    .addBooleanSwitch({
      path: 'showIsRatio',
      name: 'Is Ratio',
      description: 'Show ratio image for panel',
      defaultValue: true,
    });
});
